import React, { Component } from 'react';
import logo from './logo.svg';
import {Nav, NavItem, NavDropdown, Navbar} from 'react-bootstrap/lib/';
import './App.css';

class App extends Component {
  render() {
    return (
      <div className="App">
        <Navbar inverse>
          <Navbar.Header>
            <Navbar.Brand>
              <a href="">Home</a>
            </Navbar.Brand>
          </Navbar.Header>
          <Nav>
            <NavItem eventKey={1}>
              About me
            </NavItem>
            <NavItem eventKey={2}>
              Projects
            </NavItem>
            <NavItem eventKey={3}>
              Gallery
            </NavItem>
          </Nav>
        </Navbar>
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
        </header>
      </div>
    );
  }
}

export default App;



